<template>

    <div  class="mypanel">

        <div class="hd">
            <h2>地址管理</h2>
            <router-link to="/huiyuan/receaddressadd">新增地址</router-link>
        </div>
        <div class="bd">
            <div class="address-list clearfix">

            <ul v-for="address in listReceaddress" :key="address.id" class="address-info">
                <div class="add-title">
                    <a  :href="'#/huiyuan/receaddressadd?id='+address.id" class="edit">
                        <i class="fa fa-edit"></i>
                    </a>
                    {{address.title }}
                    <span @click="deleteOne(address.id)"  class="remove">
				           <i class="fa fa-close"></i>
                      </span>
                </div><!-- end title -->

                <li>{{address.shr}}</li>
                <li>{{address.addinfo }}</li>
                <li>{{address.mobile }}</li>
                <li>{{address.postcode}}</li>
            </ul>


            <div v-if="listReceaddress!=null&&listReceaddress.length==0" class="no-record-tip">
                <div class="content">

                    <i class="fa fa-warning"></i>暂无收货地址
                </div>

            </div>



        </div>
        </div>
    </div>

</template>

<script>
    export default {
        name: "receaddressmanager",
        data(){
            return {
                listReceaddress:[],
                hostHead:this.$app.contextPath
            }
        },
        props:{
            accountInfo:{
                type:Object
            }
        },
        methods:{
            async list(){
                let url="admin/receaddress/list";
                let {data:res}=await this.$http.post(url,{hyid:this.accountInfo.id,ispaged:-1});
                if(res!=null&&res.stateCode<0){
                    this.$message.error(res.des);
                    return;
                }

                this.listReceaddress=res.data;
                this.addrid=this.listReceaddress[0].id;
            },
            async deleteOne(id){
                let ids=[];
                ids.push(id);
                let url="admin/receaddress/delete";
                let confirm= await this.$confirm('是否要删除信息?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                });

                let {data:res}=await this.$http.post(url, {ids});
                if (res.stateCode > 0) {
                    this.$message({
                        message:"删除成功",
                        type:'success',
                        duration:3000
                    });
                    this.list();
                }
            }
        },
        created () {
            this.list();

        }

    }
</script>

